<template>
  <div class="modal fade" id="deleteCouponModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
           <strong class="text-danger">{{ couponInfo.title }}</strong> ()
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <ActionButton
            colorStyle="danger"
            btnLabel=""
            :isLoading="isLoading"
            @click.native="deleteCoupon(couponInfo.id)"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import ActionButton from '@/components/ActionButton'
export default {
  props: {
    couponInfo: {
      type: Object
    }
  },
  components: {
    ActionButton
  },
  data(){
    return {
      isLoading: false
    }
  },
  methods: {
    deleteCoupon(id){
      this.isLoading = true
      this.$store.dispatch('coupon/deleteCoupon', id)
        .then(() => {
          this.isLoading = false
          $('#deleteCouponModal').modal('hide')
        })
    }
  },
  mounted(){
    // v-if
    $('#deleteCouponModal').modal('show')
    // modalcurrentOperateType
    $('#deleteCouponModal').on('hidden.bs.modal', () => {
      this.$emit('modalHidden')
    })
  }
}
</script>

<style>

</style>



