<template>
  <div class="modal fade" id="operateProductModal" tabindex="-1" role="dialog"
    aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span>{{ modalTitle }}</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="form-group">
                <label for="title"></label>
                <input type="text" class="form-control" id="title"
                  placeholder=""
                  v-model="productData.title"
                >
              </div>
              <div class="form-group">
                <label for="image"></label>
                <input type="text" class="form-control" id="image"
                  placeholder=""
                  v-model="productData.imageUrl"
                >
              </div>
              <div class="form-group">
                <label for="customFile"> 
                  <v-icon name="spinner" pulse
                    v-if="isUploadingLoading"
                  />
                </label>
                <input type="file" id="customFile" class="form-control"
                  ref="file"
                  @change="uploadFile"
                >
              </div>
              <img class="img-fluid"
                v-if="productData.imageUrl"
                :src="productData.imageUrl"
              >
            </div>
            <div class="col-sm-8">
              <div class="form-row">
                <div class="form-group col-md-6">
                <label for="title"></label>
                <input type="text" class="form-control" id="title"
                  placeholder=""
                  v-model="productData.category.brand"
                >
              </div>
                <div class="form-group col-md-6">
                  <label for="size"></label>
                  <input type="number" class="form-control" id="size"
                    placeholder=""
                    v-model.number="productData.amount"
                  >
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category"></label>
                  <input type="text" class="form-control" id="category"
                    placeholder=""
                    v-model="productData.category.type"
                  >
                </div>
                <div class="form-group col-md-6">
                  <label for="unit"></label>
                  <input type="unit" class="form-control" id="unit"
                    placeholder=""
                    v-model="productData.unit"
                  >
                </div>
              </div>

              <div class="form-row">
                <div class="form-group col-md-6">
                <label for="origin_price"></label>
                  <input type="number" class="form-control" id="origin_price"
                    placeholder=""
                    v-model.number="productData.origin_price"
                  >
                </div>
                <div class="form-group col-md-6">
                  <label for="price"></label>
                  <input type="number" class="form-control" id="price"
                    placeholder=""
                    v-model.number="productData.price"
                  >
                </div>
              </div>
              <hr>

              <div class="form-group">
                <label for="description"></label>
                <textarea type="text" class="form-control" id="description"
                  placeholder=""
                  v-model="productData.description"
                ></textarea>
              </div>
              <div class="form-group">
                <label for="content"></label>
                <textarea type="text" class="form-control" id="content"
                  rows="3"
                  placeholder=""
                  v-model="productData.content"
                ></textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input class="form-check-input" type="checkbox"
                    id="is_enabled"
                    v-model="productData.is_enabled"
                    :true-value="1"
                    :false-value="0"
                  >
                  <label class="form-check-label" for="is_enabled">
                    
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
          <ActionButton
            colorStyle="primary"
            btnLabel=""
            :isLoading="isLoading"
            @click.native="confirm"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import cloneDeep from 'lodash/clonedeep'  // template
import ActionButton from '@/components/ActionButton'
export default {
  props: {
    operateType: {
      type: String,
      validator: string => ['add', 'edit'].indexOf(string) !== -1
    },
    productInfo: {
      type: Object,
      default(){        
        return this.$store.state.product.productTemplate
      }
    }
  },
  components: {
    ActionButton
  },
  data(){
    return {
      // templateprops
      productData: cloneDeep(this.productInfo), 
      isLoading: false,
      isUploadingLoading: false,
    }
  },
  computed: {
    modalTitle(){
      return this.operateType === 'add'
        ? ''
        : ''
    }
  },
  methods: {
    uploadFile(){
      this.isUploadingLoading = true
      const uploadedFile = this.$refs.file.files[0]      
      const formData = new FormData()
      formData.append('file-to-upload', uploadedFile)

      const API = `${process.env.API_PATH}/api/${process.env.CUSTOM_API_PATH}/admin/upload`
      this.$http.post(API, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
        .then( ({data}) => {
          this.isUploadingLoading = false

          this.$store.dispatch('alert/updateMessage', {
            message: data.success === true
              ? ''
              : ''
            ,
            status: data.success === true
              ? 'success'
              : 'danger'
          })

          if(data.success){
            this.$set(this.productData, 'imageUrl', data.imageUrl)
          }
        })
        .catch( (err) => {
          this.isUploadingLoading = false
          console.error(err)
        })
    },
    confirm(){
      console.log('' + this.operateType)

      if(this.productData.price > this.productData.origin_price){
        this.$store.dispatch('alert/updateMessage', {
          message: '',
          status: 'danger'
        })
        return
      }

      this.isLoading = true
      this.$store.dispatch(`product/${this.operateType}Product`, this.productData)
        .then(() => {
          this.isLoading = false
          $('#operateProductModal').modal('hide')
        })
    },
  },
  mounted(){
    // v-if
    $('#operateProductModal').modal('show')
    // modalcurrentOperateType
    $('#operateProductModal').on('hidden.bs.modal', () => {
      this.$emit('modalHidden')
    })
  }
}
</script>



