<template>
  <div>
    <loading :active.sync="isPageLoading"/>
    <div class="mt-4">
      <button
        class="btn btn-primary"
        @click="showModal( { type: 'add' } )"
      >
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="100"></th>
          <th width="60"></th>
          <th width="80"></th>
          <th width="80"></th>
          <th width="100"></th>
          <th width="120"></th>
        </tr>
      </thead>
      <tbody>
        <ProductItem
          v-for="product in products"
          :key="product.id"
          :productInfo="product"
          @editProduct="showModal"
          @deleteProduct="showModal"
        />
      </tbody>
    </table>

    <Pagination
      v-if="pagination.total_pages > 1"
      module="product"
      api="getProducts"
    />

    <!-- Modal -->
    <ProductModal
      v-if="currentOperateType === 'add' || currentOperateType === 'edit'"
      :operateType="currentOperateType"
      :productInfo="tempOperateData"
      @modalHidden="currentOperateType = ''"
    />
    <DeleteProductModal
      v-if="currentOperateType === 'delete'"
      :productInfo="tempOperateData"
      @modalHidden="currentOperateType = ''"
    />

  </div>
</template>

<script>
import { mapState } from 'vuex'
import ProductItem from '@/components/ProductItem'
import ProductModal from '@/components/ProductModal'
import DeleteProductModal from '@/components/DeleteProductModal'
import Pagination from '@/components/Pagination'
export default {
  components: {
    ProductItem,
    ProductModal,
    DeleteProductModal,
    Pagination
  },
  data(){
    return {
      tempOperateData: {},
      currentOperateType: '' // add edit delete 
    }
  },
  computed: {
    ...mapState('product' , ['products', 'pagination', 'isPageLoading']),
  },
  methods: {
    showModal( { data = null, type } ){
      // modaldata
      this.tempOperateData = data || this.$store.state.product.productTemplate
      this.currentOperateType = type
      // modalmounted
    },
  },
  mounted() {
    this.$store.dispatch('product/getProducts')
  },
}

// modal
// 
// currentOperateType ->
// modalv-ifmodalmodal props -> 
// modaldatamounted
// 
//  -> currentOperateType -> 
// modalmodal
</script>

<style>

</style>



